.category-container{
  min-width: 30%;
  height: 240px;
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  margin: 0 7.5px 15px;
  overflow: hidden;

  &:hover{
    cursor: pointer;
    & .background-image {
      transform: scale(1.1);
      transition: transform 5s cubic-bezier(0.25, 0.45, 0.45, 0.95);
    }
    & .category-body-container {
      opacity: 0.85;
    }
  }

  .background-image{
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
  }

  .category-body-container{
    height: 90px;
    padding: 0 25px;
    display: flex;
    position: absolute;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    background-color: white;
    opacity: 0.7;
    h2 {
      font-weight: bold;
      margin: 0 6px 0;
      font-size: 22px;
      color: #4a4a4a;
    }
    p {
      font-weight: lighter;
      font-size: 16px;
    }
  }
}
